var getPrecision = function(address) {
    var precision = [];

    address.batiment && precision.push('bat. ' + address.batiment)
    address.etage && precision.push('tage ' + address.etage)
    address.code && precision.push('code ' + address.code)

    if (precision.length) {
        return '(' + precision.join(' - ') + ')';
    }
    return '';
}

